import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useFinanceAllOtherExamFeesList } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import style from "../../FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import AddOtherFees from "../AddOtherFees/AddOtherFees";
import FinanceOtherFeesCard from "../FinanceOtherFeesCard";
const ExamOtherFees = ({
  fid,
  instituteId,
  isCashierAccess,
  collectStaffId,
}) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    financeAllOtherExamFeesList,
    financeAllOtherExamFeesListLoading,
    financeAllOtherExamFeesListRefetch,
  } = useFinanceAllOtherExamFeesList({
    data: {
      fid: fid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !fid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeAllOtherExamFeesList?.list?.length) {
      let obj = nextPage(financeAllOtherExamFeesList?.list?.length);
      setState(() => obj);
    }
  }, [financeAllOtherExamFeesList?.list?.length]);

  const onSearchEvent = (e) => {
    if (
      !financeAllOtherExamFeesListLoading &&
      financeAllOtherExamFeesList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    financeAllOtherExamFeesListRefetch();
  };
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
              />
            </div>
          </section>
          <CreateButton
            label="add"
            customStyle={{
              paddingBlock: "0.4rem",
              margin: "0",
            }}
            onAction={onToggle}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        {financeAllOtherExamFeesList?.list?.map((fees, index) =>
          financeAllOtherExamFeesList?.list?.length === index + 1 ? (
            <div key={fees?._id} ref={ref}>
              <FinanceOtherFeesCard
                fees={fees}
                onRefetch={onRefetch}
                financeId={fid}
                whichOtherFees="Other_fees"
                instituteId={instituteId}
                collectStaffId={collectStaffId}
                isCashierAccess={isCashierAccess}
              />
            </div>
          ) : (
            <FinanceOtherFeesCard
              fees={fees}
              key={fees?._id}
              onRefetch={onRefetch}
              financeId={fid}
              whichOtherFees="Other_fees"
              instituteId={instituteId}
              collectStaffId={collectStaffId}
              isCashierAccess={isCashierAccess}
            />
          )
        )}

        {financeAllOtherExamFeesListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
          )
        ) : search ? (
          financeAllOtherExamFeesList?.list?.length === 0 && (
            <EmptyMessage
              title="No item found related this search."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        ) : (
          financeAllOtherExamFeesList?.list?.length === 0 && (
            <EmptyMessage
              title="No item."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        )}
      </UserMemberContentWrapper>
      {add && (
        <AddOtherFees
          onClose={onToggle}
          fid={fid}
          onRefetch={onRefetch}
          instituteId={instituteId}
          whichOtherFees="EXAM_FEES"
        />
      )}
    </>
  );
};

export default ExamOtherFees;
