import { useTranslation } from "react-i18next";
import style from "../FinanceManager.module.css";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import { useCallback, useEffect, useState } from "react";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import NormalOtherFees from "./NormalOtherFees/NormalOtherFees";
import SubjectOtherFees from "./SubjectOtherFees/SubjectOtherFees";
import OtherTransactionHistory from "./OtherTransactionHistory/OtherTransactionHistory";
import ExamOtherFees from "./ExamOtherFees/ExamOtherFees";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useFinanceMiscellaneousFeeMessageSetting } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";

import Switch from "@mui/material/Switch";
import QvipleLoader from "../../../Loader/QvipleLoader";
import FinanceMiscellaneousStudentMessage from "./FinanceMiscellaneousStudentMessage/FinanceMiscellaneousStudentMessage";
const label = { inputProps: { "aria-label": "Switch demo" } };

const FinanceOtherFees = ({
  fid,
  instituteId,
  cashAuthority,
  masterData,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    message_bool: false,
  });
  const [step, setStep] = useState("");
  const [financeMiscellaneousFeeMessageSetting] =
    useFinanceMiscellaneousFeeMessageSetting();

  useEffect(() => {
    if (masterData?.message_bool) {
      setState((prev) => ({
        ...prev,
        message_bool: masterData?.message_bool === "On" ? true : false,
      }));
    }
  }, [masterData?.message_bool]);
  const onNavigate = useCallback(() => {
    let url = "";
    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/finance`
        : `/q/${params.username}/member/staff/finance`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  const onNavigateReceipt = useCallback(() => {
    let url = "";
    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/finance?type=miscellaneous-receipt`
        : `/q/${params.username}/member/staff/finance?type=miscellaneous-receipt`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  const onMiscellaneousFeeMessage = (e) => {
    if (fid) {
      setState((prev) => ({
        ...prev,
        message_bool: e.target.checked,
      }));
      setDisabled((pre) => !pre);
      financeMiscellaneousFeeMessageSetting({
        fid: fid,
        bodyParameter: {
          message_bool: e.target.checked ? "On" : "Off",
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onCloseStep = () => {
    setStep("");
  };
  // console.info("masterData", masterData);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "0.2rem",
              }}
            >
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onNavigate}
              />
              <h6>{t("miscellaneous_fees")}</h6>
            </div>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "1rem",
              }}
            >
              <div>
                <Switch
                  {...label}
                  checked={state.message_bool}
                  onChange={onMiscellaneousFeeMessage}
                />
                <span>{t("send_miscellaneous_fee_message")}</span>
              </div>
              {state.message_bool ? (
                <CreateButton
                  label="message"
                  onAction={() => setStep(1)}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 1rem",
                  }}
                />
              ) : null}
              {/* <CreateButton
              label="new_receipt"
              onAction={onNavigateReceipt}
              customStyle={{
                margin: "0",
                padding: "0.3rem 1rem",
              }}
            /> */}
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={
                getQuery.state?.urlOffSet === "Institute"
                  ? `/q/${params.username}/institute/member/finance?type=other-fees${tab.urlKey}`
                  : `/q/${params.username}/member/staff/finance?type=other-fees${tab.urlKey}`
              }
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />

        {searchParams.get("n") === "subject" ? (
          <SubjectOtherFees
            fid={fid}
            instituteId={instituteId}
            isCashierAccess={
              cashAuthority?.includes(getQuery?.state?.sid) ? "ALLOW" : "DENY"
            }
            collectStaffId={getQuery?.state?.sid}
          />
        ) : searchParams.get("n") === "history" ? (
          <OtherTransactionHistory fid={fid} instituteId={instituteId} />
        ) : searchParams.get("n") === "normal" ? (
          <NormalOtherFees
            fid={fid}
            instituteId={instituteId}
            isCashierAccess={
              cashAuthority?.includes(getQuery?.state?.sid) ? "ALLOW" : "DENY"
            }
            collectStaffId={getQuery?.state?.sid}
          />
        ) : searchParams.get("n") === "exam" ? (
          <ExamOtherFees
            fid={fid}
            instituteId={instituteId}
            isCashierAccess={
              cashAuthority?.includes(getQuery?.state?.sid) ? "ALLOW" : "DENY"
            }
            collectStaffId={getQuery?.state?.sid}
          />
        ) : (
          <NormalOtherFees
            fid={fid}
            instituteId={instituteId}
            isCashierAccess={
              cashAuthority?.includes(getQuery?.state?.sid) ? "ALLOW" : "DENY"
            }
            collectStaffId={getQuery?.state?.sid}
          />
          // <OtherOutsandingFees fid={fid} instituteId={instituteId} />
        )}
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
      {step === 1 ? (
        <FinanceMiscellaneousStudentMessage
          onClose={onCloseStep}
          fid={fid}
          onRefetch={onRefetch}
          menuData={masterData?.student_message}
        />
      ) : null}
    </>
  );
};

export default FinanceOtherFees;
const tabList = [
  // {
  //   label: "outstanding_fees",
  //   id: "outstanding_fees",
  //   urlKey: "",
  //   groupIs: ["", null],
  // },
  {
    label: "normal_other_fees",
    id: "normal",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "exam_other_fees",
    id: "exam",
    urlKey: "&n=exam",
    groupIs: ["exam"],
  },
  // {
  //   label: "normal_other_fees",
  //   id: "normal",
  //   urlKey: "&n=normal",
  //   groupIs: ["normal"],
  // },
  {
    label: "subject_other_fees",
    id: "subject",
    urlKey: "&n=subject",
    groupIs: ["subject"],
  },
  {
    label: "transaction_history",
    id: "history",
    urlKey: "&n=history",
    groupIs: ["history"],
  },
];
